import React from "react";
import styled from "styled-components";
const PostRow = (props) => {
    const {title, 
        writer, 
        createDate, 
        view, 
        like,
        userId,
        nickname,
        joinDate,
        role,
        id,
        contents,
        requestBoard,
        requestDate
     } = props;
    return (
        <SubjectBox>
            {title && <Title>{title}</Title>}
            {writer && <Post>{writer}</Post>}
            {createDate && <Post>{createDate}</Post>}
            {view && <Post>{view}</Post>}
            {like && <Post>{like}</Post>}
            {userId && <Post>{userId}</Post>}
            {nickname && <Post>{nickname}</Post>}
            {joinDate && <Post>{joinDate}</Post>}
            {role && <Post>{role}</Post>}
            {id && <Post>{id}</Post>}
            {contents && <Post>{contents}</Post>}
            {requestBoard && <Post>{requestBoard}</Post>}
            {requestDate && <Post>{requestDate}</Post>}
        </SubjectBox>            
    );
};


const SubjectBox = styled.div`
    display:grid;
    grid-template-columns: repeat(auto-fit, minmax(15%, auto));
    border-top:2px solid #3185FC;
    border-bottom:2px solid #3185FC;
    width:100%;
`
const Post = styled.div`
    /* margin-left:1%; */
    text-align:center;
`

const Title = styled.div`
    grid-column: 1 / 3;
    text-align:center;
`

export default PostRow;